.footer {
  min-height: 108px;
  background-color: $color-black-russian;

  @include vp-1279 {
    min-height: 114px;
  }

  @include vp-767 {
    min-height: 260px;
  }
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  gap: 30px;
  align-items: center;
  margin: 0 auto;
  padding: 36px 0 30px 0;
  max-width: 1180px;
  color: $color-default-white;

  @include vp-1279 {
    padding: 44px 0 38px 0;
  }

  @include vp-767 {
    flex-direction: column;
    align-items: flex-start;
    gap: 4px;
    padding: 42px 0 46px 0;
  }
}

.footer__wrapper ul {
  list-style-type: none;
}

.footer__wrapper a {
  display: inline-block;
  padding: 10px;
  color: $color-default-white;
}

.footer__logo {
  margin: 5px 0 0 0;
  min-width: 108px;

  @include hover {
    opacity: 70%;
  }

  @include vp-1279 {
    margin: 0;
    width: 100px;
    min-width: 100px;
    height: auto;
  }

  @include vp-767 {
    margin: 0;
    width: 87px;
    min-width: 87px;
  }
}

.footer__navigation-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin: -4px 0 0 4px;
  padding: 0;
  min-width: 445px;

  @include vp-1279 {
    gap: 10px;
    margin: -4px 0 0 5px;
    min-width: 405px;
  }

  @include vp-767 {
    display: block;
    columns: 2 150px;
    margin: -40px 0 17px 0;
    min-width: 0;
  }
}

.footer__navigation-item {
  position: relative;
  font-size: 14px;
  line-height: 16.4px;

  &::before {
    content: "";
    position: absolute;
    bottom: 5px;
    left: 10px;
    width: calc(100% - 20px);
    height: 1px;
    background-color: $color-default-white;
    transform: scale(0);
  }

  @include hover {
    &::before {
      transform: scale(1);
    }
  }

  &:active {
    opacity: 50%;
  }

  @include vp-767 {
    margin: 0 0 1px 0;
    font-size: 16px;
    line-height: 19px;
  }
}

.footer__navigation-item:first-child {
  @include vp-767 {
    margin: 40px 0 0 0;
  }
}

.footer__navigation-link:focus {
  outline: 1px solid $color-default-white;
  outline-width: 1px;
}

.footer__social-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 1px;
  margin: -7px 6px 0 0;
  padding: 0;
  min-width: 107px;
  max-width: 215px;

  @include vp-1279 {
    margin: -5px -1px 0 0;
    max-width: 107px;
  }

  @media (min-width: 900px) {
    max-width: 215px;
  }

  @include vp-767 {
    margin: -1px 0 0 5px;
    max-width: 100%;
  }
}

.footer__social-item {
  position: relative;
  width: 25px;
  height: 25px;
}

.footer__social-link {
  padding: 5px;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: $color-default-white;

  @include hover-focus {
    background-color: $color-egyptian-blue;
    color: $color-default-white;
  }

  &:active {
    background-color: $color-sapphire;
  }
}

.footer__navigation-link.is--disabled {
  opacity: 30%;
  pointer-events: none;
  cursor: default;
}


.footer__social-list svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: $color-black-russian;

  @include hover-focus {
    color: $color-default-white;
  }
}
